import React from "react";
import { FiSearch } from "react-icons/fi";

const Searchbar = () => {
  return (
    <div style={{ position: "relative" }}>
      <input type="text" className="search" style={{ borderRadius: "1rem" }} />
      <FiSearch
        style={{
          position: "absolute",
          right: "20rem",
          top: "0.7rem",
        }}
      ></FiSearch>
    </div>
  );
};

export default Searchbar;
