import React from "react";
import productimg from "../assets/controller.png";

const Productbox = () => {
  return (
    <div
      style={{
        background: "linear-gradient(to right, #ff6a00,#ee0979)",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        width: "50%",
        margin: "auto auto",
        borderRadius: "1rem",
        height: "15%",
        padding: "1rem",
      }}
    >
      <span style={{ color: "#e7e7e7", fontWeight: "bolder" }}>
        PRODUCT TEXT
      </span>
      <img
        src={productimg}
        alt="logo"
        style={{ height: "90%", transform: "rotate(-60deg)" }}
      />
    </div>
  );
};

export default Productbox;
