import React from "react";
import Progressbar from "./Progressbar";
import Searchbar from "./Searchbar";
import Assassin from "../assets/assassins.jpg";
import batman from "../assets/batman.jpeg";
import spiderman from "../assets/spiderman.jpg";

const Maincontent = () => {
  return (
    <div>
      <div style={{ margin: "5rem 5rem 0 5rem", color: "#aa0537" }}>
        <h1>TITLE</h1>
        <Searchbar></Searchbar>
      </div>
      <div
        style={{
          margin: "2rem",
          display: "flex",
          flexDirection: "column",
          gap: "2rem",
        }}
      >
        <Progressbar
          images={Assassin}
          title="Assassin creed"
          platform="PS5"
          percent="30"
        ></Progressbar>
        <Progressbar
          images={batman}
          title="Batman"
          platform="PS5"
          percent="60"
        ></Progressbar>
        <Progressbar
          images={spiderman}
          title="SpiderMan"
          platform="PS5"
          percent="82"
        ></Progressbar>
      </div>
    </div>
  );
};

export default Maincontent;
