import React from "react";
import Avatarimage from "../assets/andy.jpg";

const Profile = () => {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
        height: "25%",
        padding: "3rem 0 1rem 0",
      }}
    >
      <img
        src={Avatarimage}
        alt="Logo"
        style={{ height: "7rem", borderRadius: "5rem", objectFit: "cover" }}
      />
      <h1
        style={{
          color: "#a53b11",
          fontSize: "1.3rem",
          margin: "1rem 0 0.2rem 0",
        }}
      >
        ANDY
      </h1>
      <h2 style={{ color: "#a53b11", fontSize: "1rem" }}>GAMER</h2>
    </div>
  );
};

export default Profile;
