import React from "react";
import Icons from "./Icons";
import { Route, Switch, NavLink } from "react-router-dom";

const Links = ({ text, svimg }) => {
  return (
    <div
      style={{
        display: "flex",
        margin: "0.5rem 0",
        color: "#a53b11",
        fontWeight: "500",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <Icons svimg={svimg}></Icons>
      <div
        style={{
          paddingLeft: "1rem",
          paddingRight: "2rem",
          cursor: "pointer",
        }}
      >
        <NavLink activeClassName="color" to={text}>
          {text}
        </NavLink>
      </div>
    </div>
  );
};

export default Links;
