import React from "react";

const Progressbar = ({ images, title, platform, percent }) => {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "space-around",
        background: "rgba(255, 255, 255, 0.2)",
        alignItems: "center",
        backdropFilter: "blur(18px)",
        width: "40vw",
        height: "7rem",
        borderRadius: "1rem",
        boxShadow:
          "rgba(0,0,0,0.2) 0px 12px 28px 0px, rgba(0,0,0,0.1) 0px 2px 4px 0px,rgba(255,255,255,0.5) 0px 0px 0px 1px",
      }}
    >
      <img
        src={images}
        alt="logo"
        style={{ height: "5rem", width: "5rem", borderRadius: "1rem" }}
      />
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          width: "16rem",
        }}
      >
        <h3
          style={{
            fontSize: "1.4rem",
          }}
        >
          {title}
        </h3>
        <h4
          style={{
            fontSize: "0.9rem",
            marginBottom: "0.6rem",
            color: "#aa0537",
          }}
        >
          {platform}
        </h4>
        <progress value={percent} max="100" className="bar"></progress>
      </div>
      {percent}%
    </div>
  );
};

export default Progressbar;
