import React from "react";
import Links from "./Links";
import Profile from "./Profile";
import { ImTwitch, ImBook } from "react-icons/im";
import { IoLogoGameControllerB } from "react-icons/io";
import Productbox from "./Productbox";
import { Route, Switch, NavLink } from "react-router-dom";
import Maincontent from "./Maincontent";

const Sidebar = () => {
  return (
    <div
      style={{
        width: "30%",
        height: "100%",
        bakground: "rgba(255, 255, 255, 0.1)",
        backdropFilter: "blur(14px)",
        borderTopLeftRadius: "10px",
        borderBottomRightRadius: "10px",
      }}
    >
      <Profile></Profile>
      <div
        style={{
          paddingLeft: "2rem ",
          margin: "3rem 0",
        }}
      >
        <NavLink
          activeClassName="color"
          to="/new"
          style={{
            display: "flex",
            color: "#a53b11",
            fontWeight: "500",
            fontSize: "2rem",
            justifyContent: "center",
          }}
        >
          Home
        </NavLink>
        <Links
          text="game1"
          svimg={
            <ImTwitch style={{ fill: "url(#gradient)", fontSize: "2rem" }} />
          }
        ></Links>
        <Links
          text="game2"
          svimg={
            <IoLogoGameControllerB
              style={{ fill: "url(#gradient)", fontSize: "2rem" }}
            />
          }
        ></Links>
        <Links
          text="game3"
          svimg={
            <ImBook style={{ fill: "url(#gradient)", fontSize: "2rem" }} />
          }
        ></Links>
      </div>
      <Productbox></Productbox>
    </div>
  );
};

export default Sidebar;
